import LinkedUpgradeLine from "./LinkedUpgradeLine";
import { useLocalizeUpgrade } from "../contexts/LocalizationContext";
import { reverse_increment } from "../consts/upgrades";

const BLOCK_LIST = [
    "operator",
    "multiplicative_target",
    "multiplicative_base_value",
];

export default function UpgradeBlock({ abilityName, name, data }) {
    const defaultValue = data.value;
    const maxCount = data.max_count;
    const LocalizeUpgrade = useLocalizeUpgrade();

    let locUpgrade = LocalizeUpgrade(abilityName, name);
    const valueSymbol = reverse_increment.includes(name) ? "-" : "+";

    let linkedUpgrades = [];
    if (data.linked_special_values) {
        for (const [lSpecName, lSpecValue] of Object.entries(
            data.linked_special_values
        )) {
            if (BLOCK_LIST.includes(lSpecName)) continue;

            const locTest = LocalizeUpgrade(abilityName, lSpecName);
            if (locUpgrade === "" && locTest !== "") {
                locUpgrade = locTest;
            }

            linkedUpgrades.push(
                <LinkedUpgradeLine
                    name={lSpecName}
                    value={
                        typeof lSpecValue == "object"
                            ? lSpecValue.value
                            : lSpecValue
                    }
                    sourceAbilityName={abilityName}
                />
            );
        }
    }

    const is_pct = locUpgrade.charAt(0) == "%";
    locUpgrade = locUpgrade.replace(/%|:/g, "").trim();

    if (locUpgrade == "") locUpgrade = name;

    if (data.linked_abilities) {
        for (const [lAbilityName, lSpecialsData] of Object.entries(
            data.linked_abilities
        )) {
            if (!lSpecialsData) continue;

            for (const [lSpecName, lSpecValue] of Object.entries(
                lSpecialsData
            )) {
                if (BLOCK_LIST.includes(lSpecName)) continue;

                linkedUpgrades.push(
                    <LinkedUpgradeLine
                        name={lSpecName}
                        value={
                            typeof lSpecValue == "object"
                                ? lSpecValue.value
                                : lSpecValue
                        }
                        sourceAbilityName={abilityName}
                        linkedAbility={lAbilityName}
                    />
                );
            }
        }
    }

    const bHasMaxCount = maxCount !== undefined;
    let maxCountBlock = undefined;
    if (bHasMaxCount) {
        maxCountBlock = (
            <div className="upgrade-max-count">{`Max: ${maxCount}`}</div>
        );
    }

    return (
        <div
            className={`upgrade-block ${bHasMaxCount ? "has-max-count" : ""} ${
                linkedUpgrades.length > 0 ? "has-linkeds" : ""
            }`}
        >
            <div className="upgrade-basic-value-container">
                <div className="upgrade-basic-value">
                    <span
                        dangerouslySetInnerHTML={{
                            __html: locUpgrade.toUpperCase(),
                        }}
                    ></span>
                    : {defaultValue > 0 ? valueSymbol : ""}
                    {defaultValue}
                    {is_pct ? "%" : ""}
                </div>
                {maxCountBlock}
            </div>

            <div className="linked-upgrades-container">{linkedUpgrades}</div>
        </div>
    );
}
