import { createContext } from "react";
import { useState, useEffect, useContext } from "react";
import * as VDF from "@node-steam/vdf";
import _ from "lodash";

export const LocalizationContext = createContext({});

const LANG_ALLIASES = {
    en: "english",
    ru: "russian",
};

export function SetLanguage(lang, setLocalization) {
    document.documentElement.lang = lang;

    Promise.all([
        fetch(
            `${process.env.PUBLIC_URL}/localization/abilities_${LANG_ALLIASES[lang]}.txt`
        ).then((res) => res.text()),
        fetch(
            `${process.env.PUBLIC_URL}/localization/addon_${LANG_ALLIASES[lang]}.txt`
        ).then((res) => res.text()),
    ]).then((loc) => {
        let _loc = Object.assign(
            VDF.parse(loc[0]).lang.Tokens,
            VDF.parse(loc[1]).lang.Tokens
        );

        setLocalization(
            _.mapKeys(_loc, (_v, k) => {
                return k.toLowerCase();
            })
        );
    });
}

export function useLocalize() {
    const { localization } = useContext(LocalizationContext);

    return (key) => {
        key = key.toLowerCase();
        return localization[key] || key;
    };
}

export function useLocalizeUpgrade() {
    const { localization } = useContext(LocalizationContext);

    return (abilityName, name) => {
        let locUpgrade = "";
        let check_localize = (key) => {
            key = key.toLowerCase();

            let loc_line = localization[key];
            if (loc_line && loc_line != key) locUpgrade = loc_line;
        };

        check_localize(`${name}_upgrade`);
        check_localize(`DOTA_Tooltip_ability_${abilityName}_${name}`);
        check_localize(`upgrade_DOTA_Tooltip_ability_${abilityName}_${name}`);
        check_localize(
            `DOTA_Tooltip_ability_${abilityName.replace("_lua", "")}_${name}`
        );
        check_localize(
            `upgrade_DOTA_Tooltip_ability_${abilityName.replace(
                "_lua",
                ""
            )}_${name}`
        );
        locUpgrade = locUpgrade.replace(/\\n/g, "");
        return locUpgrade.toUpperCase();
    };
}

export function LocalizationProvider({ children }) {
    const [localization, setLocalization] = useState({});

    const changeLanguage = (lang) => {
        SetLanguage(lang, setLocalization);
    };

    useEffect(() => {
        changeLanguage("en");
    }, []);

    return (
        <LocalizationContext.Provider value={{ localization, changeLanguage }}>
            {children}
        </LocalizationContext.Provider>
    );
}
