import HeroButton from "./HeroButton";

import { STATS_ENUM } from "../consts/heroes";
import { useContext } from "react";
import { HeroesListContext } from "../contexts/HeroesListContext";
import { Tooltip } from "react-tooltip";

export default function HeroesList({ search }) {
    const parsedHeroesList = {
        [STATS_ENUM.STR]: [],
        [STATS_ENUM.AGI]: [],
        [STATS_ENUM.INT]: [],
        [STATS_ENUM.ALL]: [],
    };
    const heroes = useContext(HeroesListContext);

    const sortedHeroes = Object.keys(heroes).sort((a, b) => {
        let result = 0;
        const locNameA = heroes[a].GetLocName();
        const locNameB = heroes[b].GetLocName();

        if (locNameA < locNameB) result = -1;
        else if (locNameA > locNameB) result = 1;

        return result;
    });

    for (let heroName of sortedHeroes) {
        const _stat = heroes[heroName].GetStat();
        let activeClass = "";
        const _locName = heroes[heroName].GetLocName();
        if (search !== "") {
            activeClass = _locName.toLowerCase().includes(search.toLowerCase())
                ? "active-search"
                : "not-active-search";
        }

        parsedHeroesList[_stat].push(
            <HeroButton
                name={heroName}
                locName={_locName}
                key={heroName}
                activeClass={activeClass}
            ></HeroButton>
        );
    }

    return (
        <div className="heroes-list-container">
            <div className="heroes-list str">
                {parsedHeroesList[STATS_ENUM.STR]}
            </div>
            <div className="heroes-list agi">
                {parsedHeroesList[STATS_ENUM.AGI]}
            </div>
            <div className="heroes-list int">
                {parsedHeroesList[STATS_ENUM.INT]}
            </div>
            <div className="heroes-list all">
                {parsedHeroesList[STATS_ENUM.ALL]}
            </div>
            <Tooltip id="hero-name-tooltip" />
        </div>
    );
}
