import { createContext } from "react";
import { useState, useEffect } from "react";

export const HeroesListContext = createContext({});

class Hero {
    constructor(name, data) {
        this.name = name;
        this.stat = data.primary_attr;
        this.loc_name = data.name_english_loc;
    }
    GetStat() {
        return this.stat;
    }
    GetName() {
        return this.stat;
    }
    GetLocName() {
        return this.loc_name;
    }
}

export function HeroesListProvider({ children }) {
    const [heroes, setHeroesListContext] = useState({});

    useEffect(() => {
        fetch(`${process.env.PUBLIC_URL}/heroes_list.json`)
            .then((r) => r.json())
            .then((r) => {
                const _tempHeroesList = {};
                for (const hero of r)
                    _tempHeroesList[hero.name] = new Hero(hero.name, hero);

                setHeroesListContext(_tempHeroesList);
            });
    }, []);

    return (
        <HeroesListContext.Provider value={heroes}>
            {children}
        </HeroesListContext.Provider>
    );
}
