import { useParams } from "react-router-dom";
import { useState, useEffect, useContext } from "react";
// import ParseKV from "parse-kv";
import { HeroesListContext } from "../contexts/HeroesListContext";
import AbilityBlock from "../components/AbilityBlock";
import PublicImage from "../utils/PublicImage";
import * as VDF from "@node-steam/vdf";

const get_text_file = async (filepath) => {
    const res = await fetch(`${process.env.PUBLIC_URL}/${filepath}`);

    if (!res.ok) throw res;

    return res.text();
};

export default function AboutHero() {
    const { name } = useParams();
    const [abilitiesList, setAbilitiesList] = useState([]);
    const [locName, setLocName] = useState(name);
    const heroes = useContext(HeroesListContext);

    useEffect(() => {
        if (!heroes || !heroes[name]) return;
        setLocName(heroes[name].GetLocName());

        get_text_file(`upgrades/heroes/${name}.txt`)
            .then((kv) => {
                const upgrades = VDF.parse(kv)[name];

                let abilitiesListTemp = [];
                for (const [ability, upgradeData] of Object.entries(upgrades)) {
                    abilitiesListTemp.push(
                        <AbilityBlock
                            abilityName={ability}
                            upgradeData={upgradeData}
                            key={ability}
                        ></AbilityBlock>
                    );
                }
                setAbilitiesList(abilitiesListTemp);
            })
            .catch(console.error);
    }, [heroes]);

    return (
        <div className="about-hero-container">
            <div className="about-hero-info">
                <img
                    className="about-hero-icon"
                    src={PublicImage(`heroes/${name}_png`)}
                />
                {locName}
            </div>
            <div className="abilities-container">{abilitiesList}</div>
        </div>
    );
}
