import PublicImage from "../utils/PublicImage";
import { useLocalizeUpgrade } from "../contexts/LocalizationContext";
import { reverse_increment } from "../consts/upgrades";

export default function LinkedUpgradeLine({
    name,
    value,
    sourceAbilityName,
    linkedAbility,
}) {
    const LocalizeUpgrade = useLocalizeUpgrade();

    let locUpgrade = LocalizeUpgrade(linkedAbility || sourceAbilityName, name);
    if (locUpgrade == "") locUpgrade = LocalizeUpgrade(sourceAbilityName, name);

    if (locUpgrade == "") return;

    const is_pct = locUpgrade.charAt(0) == "%";
    locUpgrade = locUpgrade.replace(/%|:/g, "").trim();
    if (locUpgrade == "") locUpgrade = name;

    let image =
        linkedAbility !== undefined ? (
            <img
                className="linked-ability-image"
                src={PublicImage(`spellicons/${linkedAbility}_png`)}
            />
        ) : undefined;

    const valueSymbol = reverse_increment.includes(name) ? "-" : "+";

    return (
        <div className="linked-ability">
            <div className="tree-icon"></div>
            {image}
            <span
                dangerouslySetInnerHTML={{
                    __html: `${locUpgrade.toUpperCase()}: `,
                }}
            ></span>
            {value > 0 ? valueSymbol : ""}
            {value}
            {is_pct ? "%" : ""}
        </div>
    );
}
