import { Link } from "react-router-dom";
import PublicImage from "../utils/PublicImage";

export default function HeroButton({ name, locName, activeClass }) {
    return (
        <Link
            to={`heroes/${name}`}
            className="hero-button-container"
            data-tooltip-id="hero-name-tooltip"
            data-tooltip-content={locName}
        >
            <img
                className={`hero-button ${activeClass}`}
                src={PublicImage(`heroes/${name}_png`)}
            ></img>
        </Link>
    );
}
