import PublicImage from "../utils/PublicImage";
import UpgradeBlock from "./UpgradeBlock";
import { useLocalize } from "../contexts/LocalizationContext";

const affixesToRemove = ["ot3_", "_custom", "_lua"];
const swapAbilitiesIcons = { doom_devour_custom: "doom_bringer_devour" };

export default function AbilityBlock({ abilityName, upgradeData }) {
    let upgradesList = [];
    const Localize = useLocalize();

    for (const [upgradedVar, data] of Object.entries(upgradeData))
        upgradesList.push(
            <UpgradeBlock
                abilityName={abilityName}
                name={upgradedVar}
                data={data}
            />
        );

    return (
        <div className="ability-block">
            <div className="ability-name">
                <img
                    className="ability-icon"
                    src={PublicImage(
                        `spellicons/${affixesToRemove.reduce(
                            (line, affix) => line.replace(affix, ""),
                            swapAbilitiesIcons[abilityName] || abilityName
                        )}_png`
                    )}
                />
                {Localize(`DOTA_Tooltip_ability_${abilityName}`)}
            </div>
            <div className="ability-upgrades-container">{upgradesList}</div>
        </div>
    );
}
